@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  transition: background-color 0.1s ease;
  transition: color 0.1s ease;
  transition: border-color 0.1s ease;
}

body {
  font-family: 'Poppins', sans-serif;
  text-align: center;
}

body.light{
  background-color: #ffffff;
  color:black;
}

body.dark{
  background-color: #000000;
  color:white;
}

footer{
  bottom: 0%;
  position: fixed;
}
.filterBox {
  width: 50%;
  margin-left:25%;
  border: solid #4e4e4e;
}

.header{
  width: 100%;
  text-align: center;
  z-index: 50;
}

.header.dark{
  
  background-color: rgb(44, 44, 44);
  color: #ececec;
}
.aboutContainer{
  margin: 200px auto;
  padding: 50px;
  width: 50%;
}

.aboutContainer.light{
  border: solid #4e4e4e;
  background-color:rgb(238, 238, 238);
}

.aboutContainer.dark{
  border: solid rgb(238, 238, 238);
  background-color:#4e4e4e;

}
.serverContainer {
  text-align: left;
  border-radius: 1px;
  margin: 5px 0;
  width: 100%;
}

.serverContainer.light {
  border: solid #8d8d8d;
  background-color: #e6e6e6;
  color: rgb(0, 0, 0);
}


.serverContainer.dark {
  border: solid #d3d3d3;
  background-color: #6d6d6d;
  color: rgb(255, 255, 255);
}

.serverContainer svg{
  display: block;
  width: 100%;
  text-align: right;

}
.serverLi {
  float: center;
  position: relative;
  left: 25%;
  overflow: hidden;
  width: 50%;
}

.header.light{
  background-color: #ececec;
  color: rgb(44, 44, 44);
}

.header ul li {
  display: inline-block;
  margin: 5px 50px;
  margin: 5px calc(10vw/3);
}


.btn {
  border-radius: 2px;
  padding: 5px;
  cursor: pointer;
}

.btn.light{
  background-color: rgb(44, 44, 44);
  color:white;
}

.btn.dark{
  background-color: #272727;
  color: white;
}

.btn.dark a{
  color: white;
}
.btn.light a{
  color: white;
}
a{
  outline: none;
  text-decoration: none;
  color: black;
}
.light{
  color: black;
}
.dark{
  color: white;
}
